<template>
    <div class="container">
        
        <div id="app" v-if="!connect">
            <h1>Remoted Desktop Web Client</h1>
            <p align='center'>Enter connection information to connect</p>

            <div class="field">
                <label for="scheme">Scheme/Protocol</label>
                <input type="text" v-model="scheme" id="scheme">
            </div>

            <div class="field">
                <label for="hostname">Hostname or IP Address</label>
                <input type="text" v-model="hostname" id="hostname">
            </div>

            <div class="field">
                <label for="port">Port (if not default)</label>
                <input type="text" v-model="port" id="port">
            </div>
            <div class="field">
                <label for="domain">Domain</label>
                <input type="text" v-model="domain" id="domain">
            </div>
            <div class="field">
                <label for="user">User name</label>
                <input type="text" v-model="user" id="user">
            </div>

            <div class="field">
                <label for="pass">Password</label>
                <input type="password" v-model="pass" id="pass">
            </div>

            <div class="field">
                <label for="ignorecert">Ignore Certificate</label>
                <span>
              <input type="checkbox" v-model="ignoreCert" id="ignorecert">
            </span>
            </div>

            <div class="field">
                <label for="nla">Security</label>
                <input type="text" v-model="security" id="nla" placeholder="type nla here for Network Level Authentication">
            </div>

            <div class="field">
                <label>Query string</label>
                <span class="pl-1">{{scrubbedQuery}}</span>
            </div>

            <div class="field">
                <label for="forcehttp">Force HTTP Tunnel</label>
                <span><input type="checkbox" v-model="forceHttp" id="forcehttp"></span>
            </div>
            <div class="field">
                <label for="width">Screen Width</label>
                <input type="text" v-model="width" id="width" >
            </div>
            <div class="field">
                <label for="height">Screen Height</label>
                <input type="text" v-model="height" id="height" >
            </div>
            <div class="field">
                <label for="dpi">Resolution </label>
                <input type="text" v-model="dpi" id="dpi" >
            </div>
            <!-- <div class="field">
                <label for="color_depth">Color Depth </label>
                <input type="text" v-model="color_depth" id="color_depth" >
            </div> -->
            <div class="center">
                <button class="connect" @click="doConnect()">Connect</button>
            </div>
        </div>
        <guac-client v-else :query="query" :force-http="forceHttp"/>
    </div>
</template>

<script>
  import GuacClient from '@/components/GuacClient'
  export default {
    name: 'app',
    components: {
      GuacClient
    },
    data() {
      return {
        connect: false,

        scheme: 'rdp',
        hostname: 'localhost',
        port: '3389',
        domain:'',
        user: '',
        pass: '',
        ignoreCert: true,
        security: '',
        forceHttp: false,
        height: window.innerHeight,
        width: window.innerWidth,
        dpi:32,
        // color_depth:256
      }
    },
    computed: {
      queryObj() {
        return {
          scheme: this.scheme,
          hostname: this.hostname,
          port: this.port,
          'ignore-cert': this.ignoreCert,
          security: this.security,
          domain:this.domain,
          username: this.user,
          password: this.pass,
          height: this.height,
          width: this.width,
          dpi:this.dpi,
          // color_depth:this.color_depth
        }
      },
      query() {
        const queryString = []
        for (const [k, v] of Object.entries(this.queryObj)) {
          if (v) {
            queryString.push(`${k}=${encodeURIComponent(v)}`)
          }
        }
        return queryString.join("&")
      },
      scrubbedQuery() {
        return this.query.replace(/password=[^& ]+/, 'password=****')
      }
    },
    created() {
        window.addEventListener('resize', this.handleResize);
        this.handleResize();
    },
    destroyed() {
        window.removeEventListener('resize', this.handleResize);
    },
    methods: {
      doConnect() {
        if (window.localStorage) {
          window.localStorage.setItem('query', JSON.stringify(this.queryObj))
        }
        this.connect = true
      },
      handleResize() {
            this.width = window.innerWidth;
            this.height = window.innerHeight;
      }
    },
    mounted() {
      if (window.localStorage && window.localStorage.getItem('query')) {
        let query
        try {
          query = JSON.parse(window.localStorage.getItem('query'))
        } catch (e) {
          window.localStorage.setItem('query', '{}')
          return
        }
        this.scheme = query.scheme
        this.hostname = query.hostname
        this.port = query.port
        this.ignoreCert = query['ignore-cert']
        this.nla = query.nla
        this.domain = query.domain
        this.user = query.username
        this.pass = query.password
        
      }
    }
  }
</script>

<style>
    html, body {
        margin: 0;
        height: 100%;
        width: 100%;
    }

    .container {
        width: 100%;
        height: 100%;
    }

    #app {
        height: 100%;
        font-family: 'Avenir', Helvetica, Arial, sans-serif;
        font-size: 16pt;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
        padding: 1rem;
    }

    h1 {
        text-align: center;
    }

    .field {
        display: grid;
        grid-template-columns: 300px 1fr;
        margin-bottom: 1rem;
    }

    label {
        text-align: right;
    }

    label::after {
        content: ': '
    }

    input {
        margin-left: 1rem;
        margin-right: 1rem;
        font-size: 16pt;
        border: 1px solid black;
        border-radius: 2px;
        padding-left: 0.5rem;
    }

    .center {
        text-align: center;
    }

    .connect {
        font-size: 16pt;
        background: none;
        border: 1px solid black;
        border-radius: 5px;
        padding: .5rem 1rem;
    }

    .pl-1 {
        padding-left: 1rem;
    }
</style>
